import { useState, useRef, useCallback } from "react";
import styles from "./InputImage.module.scss";

interface InputImageProps {
  file: File | null | undefined;
  onChange: (file: File | null) => void;
}

export const InputImage = ({ file, onChange }: InputImageProps) => {
  const [preview, setPreview] = useState<string | null>(null);
  const [isDragging, setIsDragging] = useState(false);
  const fileInputRef = useRef<HTMLInputElement>(null);

  const handleFile = useCallback(
    (selectedFile: File) => {
      if (selectedFile && selectedFile.type.startsWith("image/")) {
        onChange(selectedFile);

        const reader = new FileReader();
        reader.onload = (e) => setPreview(e.target?.result as string);
        reader.readAsDataURL(selectedFile);
      }
    },
    [onChange]
  );

  const handleFileChange = (event: React.ChangeEvent<HTMLInputElement>) => {
    const selectedFile = event.target.files?.[0];
    if (selectedFile) {
      handleFile(selectedFile);
    }
  };

  const handleDragOver = (e: React.DragEvent<HTMLDivElement>) => {
    e.preventDefault();
    e.stopPropagation();
    setIsDragging(true);
  };

  const handleDragLeave = (e: React.DragEvent<HTMLDivElement>) => {
    e.preventDefault();
    e.stopPropagation();
    setIsDragging(false);
  };

  const handleDrop = (e: React.DragEvent<HTMLDivElement>) => {
    e.preventDefault();
    e.stopPropagation();
    setIsDragging(false);

    const droppedFile = e.dataTransfer.files[0];
    if (droppedFile) {
      handleFile(droppedFile);
    }
  };

  const handleRemove = () => {
    onChange(null);
    setPreview(null);
    if (fileInputRef.current) {
      fileInputRef.current.value = "";
    }
  };

  const handleClick = () => {
    fileInputRef.current?.click();
  };

  return (
    <div
      className={`${styles.input__container} ${
        isDragging ? styles.dragging : ""
      }`}
      onDragOver={handleDragOver}
      onDragLeave={handleDragLeave}
      onDrop={handleDrop}
      onClick={handleClick}
    >
      {preview ? (
        <div className={styles.preview}>
          <img src={preview} alt="Preview" className={styles.preview__image} />
          <i className="pi pi-times" onClick={handleRemove}></i>
        </div>
      ) : (
        <>
          <label className={styles.label}>
            <input
              ref={fileInputRef}
              type="file"
              className={styles.input}
              onChange={handleFileChange}
              accept="image/*"
            />
            <span>{isDragging ? "Отпустите файл" : "Загрузить файл"}</span>
          </label>
          <p className={styles.hint}>PNG, JPG, GIF до 10MB</p>
          <p className={styles.hint}>Или перетащите файл сюда</p>
        </>
      )}
    </div>
  );
};
