"use client";

import { useConstructorStore } from "@/src/store/useConstructorStore";
import { Spinner } from "@/src/ui/LoadingSpinner/LoadingSpinner";
import { TextArea } from "@/src/ui/textArea/TextArea";
import { useEffect } from "react";
import styles from "./CreateForm.module.scss";
import { ProfileContentContainer } from "@/src/Features/ProfileContentContainer/ProfileContentContainer";
import { InputImage } from "@/src/ui/InputImage/InputImage";
import { Paragraph } from "@/src/ui/p/Paragraph";
import { Button } from "@/src/ui/buttonDeafault/Button";
import Link from "next/link";

export const CreateForm = () => {
  const {
    subjects,
    getSubjects,
    setSubject,
    getTaskNumber,
    subject,
    loading,
    error,
    tasks,
    task,
    setTask,
    getVarinants,
    variants,
    variant,
    setVariant,
    answer,
    setAnswer,
    image,
    setImage,
  } = useConstructorStore();

  useEffect(() => {
    getSubjects();
    getVarinants();
  }, []);

  useEffect(() => {
    if (subject) {
      getTaskNumber(subject.ID);
    }
  }, [subject]);

  return (
    <section>
      <ProfileContentContainer>
        {loading &&
        (!subjects || subjects.length === 0) &&
        (!tasks || tasks.length === 0) ? (
          <Spinner />
        ) : (
          <form className={styles.form}>
            <div className={styles.select__container}>
              <i className={`pi pi-chevron-down ${styles.icon}`} />
              <select
                className={styles.select}
                value={subject?.Name || ""}
                onChange={(e) => {
                  const selectedSubject = subjects?.find(
                    (s) => s.Name === e.target.value
                  );
                  if (selectedSubject) setSubject(selectedSubject);
                }}
              >
                <option value="">Выберите предмет</option>
                {subjects?.map((subject) => (
                  <option key={subject.ID} value={subject.Name}>
                    {subject.Name}
                  </option>
                ))}
              </select>
            </div>
            <div className={styles.select__container}>
              <i className={`pi pi-chevron-down ${styles.icon}`} />
              <select
                className={styles.select}
                value={task?.TaskTitle || ""}
                onChange={(e) => {
                  const selectedTask = tasks?.find(
                    (t) => t.TaskTitle === e.target.value
                  );
                  if (selectedTask) setTask(selectedTask);
                }}
              >
                <option value="">Выберите категорию</option>
                {tasks?.map((task) => (
                  <option key={task.TaskID} value={task.TaskTitle}>
                    {task.TaskTitle}
                  </option>
                ))}
              </select>
            </div>
            <div className={styles.select__container}>
              <i className={`pi pi-chevron-down ${styles.icon}`} />
              <select
                className={styles.select}
                value={variant?.VariantName || ""}
                onChange={(e) => {
                  const selectedVariant = variants?.find(
                    (v) => v.VariantName === e.target.value
                  );
                  if (selectedVariant) setVariant(selectedVariant);
                }}
              >
                <option value="">Выберите вариант</option>
                {variants?.map((variant) => (
                  <option key={variant.VariantID} value={variant.VariantName}>
                    {variant.VariantName}
                  </option>
                ))}
              </select>
            </div>
            <TextArea
              onChange={(e) => setAnswer(e.target.value)}
              value={answer}
              placeholder="Введите ответ"
            />
            <div className={styles.container}>
              <Paragraph size="small">Изображение с решением</Paragraph>
              <InputImage file={image} onChange={setImage} />
            </div>
            <div className={styles.btn__container}>
              <Button outlined>Вернуться назад</Button>
              <Link href="/profile/create-task/constructor">
                <Button color="white" filled>
                  Перейти в конструктор
                </Button>
              </Link>
            </div>
          </form>
        )}
      </ProfileContentContainer>
    </section>
  );
};
